import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import History from './views/about/History.vue'
import Delivery from './views/about/Delivery.vue'
import OrderingGuide from './views/about/OrderingGuide.vue'

Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        default: Home,
        'orderingGuide': OrderingGuide,
        'delivery': Delivery,
        'history': History
      }
    },
    {
      path: '/register',
      name: 'register',
      component: () => import(/* webpackChunkName: "about" */ './views/Register.vue')
    },
    {
      path: '/login',
      name: 'login',
      component: () => import(/* webpackChunkName: "about" */ './views/Login.vue')
    },
    {
      path: '/menu',
      name: 'menu',
      component: () => import(/* webpackChunkName: "about" */ './views/Menu.vue'),
    },
    {
      path: '/about',
      name: 'about',
      redirect: "/contact",
      component: () => import(/* webpackChunkName: "about" */ './views/about/About.vue'),
      children: [
        {
          path: '/contact',
          name: 'contact',
          redirect: '/person',
          component: () => import(/* webpackChunkName: "about" */ './views/about/Contact.vue'),
          children: [
            {
              path: '/phone',
              name: 'phone',
              component: () => import(/* webpackChunkName: "about" */ './views/about/contact/Phone.vue')
            },
            {
              path: '/person',
              name: 'person',
              component: () => import(/* webpackChunkName: "about" */ './views/about/contact/Person.vue')
            },
          ]
        },
        {
          path: '/delivery',
          name: 'delivery',
          component: () => import(/* webpackChunkName: "about" */ './views/about/Delivery.vue')
        },
        {
          path: '/orderingGuide',
          name: 'orderingGuide',
          component: () => import(/* webpackChunkName: "about" */ './views/about/OrderingGuide.vue')
        },
        {
          path: '/history',
          name: 'history',
          component: () => import(/* webpackChunkName: "about" */ './views/about/History.vue')
        }
      ]
    },
    {
      path: '/admin',
      name: 'admin',
      // redirect: '/addPizza',
      component: () => import(/* webpackChunkName: "about" */ './views/Admin.vue'),
    },
    {
      path: '/addPizza',
      name: 'addPizza',
      component: () => import(/* webpackChunkName: "about" */ './views/admin/AddPizza.vue')
    },
    { path: "*", redirect: "/" }


  ]
})
