import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
import isLogin from './modules/isLogin'
import pizza from './modules/pizza'
import cart from './modules/cart'


Vue.use(Vuex)

export default new Vuex.Store({
    modules: {
        user,
        isLogin,
        pizza,
        cart
    }
})
