<template>
  <header>
    <nav class="navbar navbar-expand-lg navbar-light bg-light mb-4">
      <a class="py-2" href="#">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          stroke-linecap="round"
          stroke-linejoin="round"
          class="d-block mx-auto"
        >
          <circle cx="12" cy="12" r="10" />
          <line x1="14.31" y1="8" x2="20.05" y2="17.94" />
          <line x1="9.69" y1="8" x2="21.17" y2="8" />
          <line x1="7.38" y1="12" x2="13.12" y2="2.06" />
          <line x1="9.69" y1="16" x2="3.95" y2="6.06" />
          <line x1="14.31" y1="16" x2="2.83" y2="16" />
          <line x1="16.62" y1="12" x2="10.88" y2="21.94" />
        </svg>
      </a>
      <router-link to="/" class="navbar-brand">Pizza点餐系统</router-link>
      <ul class="navbar-nav">
        <li>
          <router-link to="/" class="nav-link">主页</router-link>
        </li>
        <li>
          <router-link to="/menu" class="nav-link">菜单</router-link>
        </li>
        <li>
          <router-link to="/admin" class="nav-link">管理</router-link>
        </li>
        <li>
          <router-link to="/about" class="nav-link">关于我们</router-link>
        </li>
      </ul>
      <ul class="navbar-nav ml-auto">
        <li v-if="!isLogin" class="nav-item">
          <router-link to="/login" class="nav-link">登录</router-link>
        </li>
        <li v-if="!isLogin" class="nav-item">
          <router-link to="/register" class="nav-link">注册</router-link>
        </li>
        <li v-if="isLogin && user != null " class="nav-item">
          <a class="nav-link" href="/logout" @click.prevent="logout">
            {{ email }}
            注销
          </a>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Nav",
  computed: {
    ...mapGetters(["isLogin", "user"]),
    email() {
      return localStorage.getItem("user");
    }
  },
  methods: {
    logout() {
      // 本地清除
      localStorage.removeItem("user");
      // vuex状态初始化
      this.$store.dispatch("setIsLoginAsync", false);
      this.$store.dispatch("setPizzaAsync", null);
      this.$store.dispatch("setUserAsync", null);
      // 页面跳转
      this.$router.push("/login");
    }
  }
};
</script>

<style scoped>
</style>